import md5 from '../../libs/md5.min.js'
export default {
    data () {
        return {
            phone: '',
            smsCode: '',
            password: '',
            confirmPassword: '',
            phoneErr: '',
            codeErr: '',
            oldPasswordErr: '',
            passwordErr: '',
            countNum: 60,
            startCount: false,
            timeObj: null,
            pageType: 'signUp',
            oldPassword: '',
            inputType: 'password'
        }
    },
    watch: {
        countNum(newVal) {
            if (newVal <= 0) {
                this.startCount = false
                clearTimeout(this.timeObj)
                setTimeout(() => {
                    this.countNum = 60
                }, 1000);
            }
        }
    },
    onLoad (option) {
        this.pageType = option.page         
        if (this.pageType === 'signUp') {
            uni.setNavigationBarTitle({
                title: '注册'
            })
        } else if (this.pageType === 'forgetPassword') {
            uni.setNavigationBarTitle({
                title: '忘记密码'
            })
        } else {
            uni.setNavigationBarTitle({
                title: '修改密码'
            })
        }
        
    },
    created() {
        this.initCountNum()
    },
    methods: {        
        async sendSms() {
            if (this.startCount) {
                return
            }
            if (!this.phone) {                
                this.phoneErr = '手机号为空'
                return
            }
            if (this.phoneErr) {
                return
            }
            let countTime = new Date().getTime()
            uni.setStorageSync('countTime',countTime)
            this.countStart()
            let params = {
                phone: this.phone,
                type : this.pageType === 'signUp' ? 'register' : 'resetPassword'
            }
            await this.$api.getSms(params)
        },
        async registerUser () {
            if (!this.checkPostData()) {
                return
            }
            let apiUrl = this.pageType === 'signUp' ? 'registerUser' : 'resetPassword'
            let params = {
                babyBirthday: '123456',
                phone : String(this.phone),
                password : md5(this.password),
                verifyCode  : this.smsCode
            }
            const res = await this.$api[apiUrl](params)
            if (res.code === '0') {
                this.$tip.toast(`${this.pageType === 'signUp' ? '恭喜，注册成功！': '密码修改成功'}`,'none')
                uni.navigateBack()
            } else if (res.code === '4') {    
                this.codeErr = '验证码错误'
                this.$tip.toast(this.codeErr,'none')
            } else {
                this.$tip.toast(res.message,'none')
            }
        },
        async confirmChange () {
            if (!this.checkChangeData()) {
                return
            }
            let apiUrl = 'updatePassword'
            let params = {
                oldPassword  : md5(this.oldPassword),
                password : md5(this.password)
            }
            const res = await this.$api[apiUrl](params)
            if (res.code === '0') {
                this.$tip.toast('修改成功','none')
                uni.navigateBack()
            } else if (res.code === '10004') {    
                this.oldPasswordErr = '旧密码错误'
                this.$tip.toast(this.oldPasswordErr,'none')
            } else {
                this.oldPasswordErr = res.message
                this.$tip.toast(res.message,'none')
            }
        },
        changeEye () {
            if (this.inputType === 'password') {
                this.inputType = 'text'
            } else {
                this.inputType = 'password'
            }
        },
        countStart () {
            this.timeObj = setInterval(() => {
                this.startCount = true
                this.countNum--
            }, 1000);
        },
        initCountNum () {            
            let temp = uni.getStorageSync('countTime')/1000
            let now = new Date().getTime()/1000
            if (now - temp < 60) {
                this.countNum = parseInt(60 - now + temp)
                this.countStart()
            }
        },
        checkPhone () {            
            if(!(/^1[123456789]\d{9}$/.test(this.phone))){ 
                this.phoneErr = '手机号格式错误'    
            }else{
                this.phoneErr = ''
            }
        },
        checkCode () {
            if (!this.smsCode) {                
                this.codeErr = '验证码不能为空'
            } else {
                this.codeErr = ''
            }
        },
        checkPassword (type) {
            let check = () => {                
                if (this.password !== this.confirmPassword) {
                    this.passwordErr = '两次输入得密码不一致'
                } else if (this.password.length < 8) {              
                    this.passwordErr = '密码字符至少为8位'
                } else {
                    this.passwordErr = ''
                }
            }
            if (type === 'confirm') {
                check()
            } else {
                if (!this.password) {
                    this.passwordErr = '密码不能为空'
                } else if (this.password.length < 8) {               
                    this.passwordErr = '密码字符至少为8位'
                } else if (this.confirmPassword) {
                    check()
                } else {
                    this.passwordErr = ''
                }
            }
        },
        checkPostData () {
            if (!this.phone) {
                this.phoneErr = '手机号不能为空'
                this.$tip.toast(this.phoneErr,'none')
                return false
            }
            if (this.phoneErr) {
                this.$tip.toast(this.phoneErr,'none')
                return false
            }
            if (!this.smsCode) {
                this.codeErr = '验证码不能为空'
                this.$tip.toast(this.codeErr,'none')
                return false
            }
            if (!this.password) {
                this.passwordErr = '密码不能为空'
                this.$tip.toast(this.passwordErr,'none')
                return false
            } else if (this.passwordErr) {
                this.$tip.toast(this.passwordErr,'none')
                return false
            }
            return true
        },
        checkChangeData () {
            if (!this.oldPassword) {
                this.oldPasswordErr = '旧密码不能为空'
                this.$tip.toast(this.oldPasswordErr,'none')
                return false
            }
            if (!this.password) {
                this.passwordErr = '密码不能为空'
                this.$tip.toast(this.passwordErr,'none')
                return false
            } else if (this.passwordErr) {
                this.$tip.toast(this.passwordErr,'none')
                return false
            }
            return true
        }
    },
}
